.abtop {
    display: flex;
    flex-wrap: wrap;
    &-text {
        &__left {

        }
        &__right {
            border-left: 4px solid #E6E6F0;
            padding-left: 30px;
            padding-top: 10px;
        }
        &__title {
            font-weight: 700;
            font-size: 30px;
            line-height: 37px;
            background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            margin-bottom: 28px;
            margin-top: 26px;

            &-txt {
                font-weight: 400;
                font-size: 19px;
                line-height: 30px;
                color: #000000;
                margin-bottom: 20px;
            }
        }
        &__num {
            font-weight: 700;
            font-size: 36px;
            line-height: 44px;
            text-align: center;
            background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
        }
        &__txt {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            color: #323264;
            margin-top: 10px;
        }
        &__item:not(:last-child) {
            margin-bottom: 40px;
        }
        
    }
}


/*========================================*/

@media screen and (min-width: 768px) {
    .abtop {
        &__text {
            width: auto;
            margin-top: 272px;
        }
        &__arrow-down {
            margin-top: 417px;
            margin-bottom: 91px;
        }
    }    

    .abtop-text__title {
        font-size: 38px;
        line-height: 46px;
        margin-top: 66px;
        margin-bottom: 26px;
    }
    .abtop-text__title-mrtop {
        margin-top: 31px;
    }
    
}

@media screen and (min-width: 1024px) {
    .abtop {
        flex-wrap: nowrap;
        &__text {
            margin-top: 250px;
        }
        &__arrow-down {
            margin-top: 288px;
            margin-bottom: 90px;
        }
        &-text__left {
            width: 84%;
            padding-right: 31px;
        }

        &-text__right {
            margin-top: 57px;
        }
        &-text__title-mrtop {
            margin-top: -3px;
        }
    }

    .abtop-text2 {
        display: flex;
        &__left {
            width: 54%;
        }
        &__right {
            width: 50%;
            padding-left: 74px;
        }
    }
}

@media screen and (min-width: 1440px) {
    .abtop-text2__left {
        width: 71%;
        padding-left: 119px;
    }
}